import React from "react";
import { observer } from "mobx-react-lite";
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import FormControl from '@mui/material/FormControl';
import Modal from "@mui/material/Modal";
import Avatar from '@mui/material/Avatar';
import CoffeeIcon from '@mui/icons-material/Coffee';
import RadioGroup from "@mui/material/RadioGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import Radio from "@mui/material/Radio";
import { store } from "store";
import { telegram } from "telegram";
import { TabPanelProps } from "./app.types";
import "./app.css";

const style = {
  position: 'absolute' as const,
  top: '50%',
  left: 'calc((390px - 300px) / 6.5)',
  transform: 'translate(0, -100%)',
  width: 300,
  bgcolor: 'background.paper',
  boxShadow: 24,
  outline: "none",
  borderRadius: 1,
  p: 4,
};

function a11yProps(index: number) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

function CustomTabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

export const App = observer(() => {
  const [value, setValue] = React.useState(0);
  const [touchStart, setTouchStart] = React.useState(0);
  const [touchEnd, setTouchEnd] = React.useState(0);

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };

  const handleTouchStart = (e: any) => {
    if (!store.selectedAddress) return;
    setTouchStart(e.targetTouches[0].clientX);
  }

  const handleTouchMove = (e: any) => {
    if (!store.selectedAddress) return;
    setTouchEnd(e.targetTouches[0].clientX);
  }

  const handleTouchEnd = () => {
    if (!store.selectedAddress) return;
    if (touchStart - touchEnd > 150) {
      // do your stuff here for left swipe
      setValue((prev) => {
        if (prev === store.source?.categories.length) return prev;

        return prev + 1;
      })
    }

    if (touchStart - touchEnd < -150) {
      // do your stuff here for right swipe
      setValue((prev) => {
        if (prev === 0) return prev;

        return prev - 1;
      })
    }
  }

  return (
    <div className="App" onTouchStart={handleTouchStart} onTouchMove={handleTouchMove} onTouchEnd={handleTouchEnd}>
      <Box sx={{ padding: 0, borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={value} onChange={handleChange} aria-label="">
          {store.source?.categories.map((category, index) => <Tab key={index} label={category.displayName} {...a11yProps(index)} />)}
        </Tabs>
      </Box>
      {store.source?.categories.map((category, index) => (
        <CustomTabPanel key={`${category.id}_${index}`} value={value} index={index}>
          <List sx={{ width: '100%', bgcolor: 'background.paper' }}>
            {category.products.map((product, index) => {
              const price = product.prices["0,2"] ?? product.prices["0,3"] ?? product.prices["0,4"] ?? product.prices["0,6"];
              return (
                <ListItem key={`${product.id}_${index}`} sx={{ ":hover": { cursor: "pointer" } }}>
                  <ListItemAvatar>
                    <Avatar>
                      <CoffeeIcon />
                    </Avatar>
                  </ListItemAvatar>
                  <ListItemText primary={product.displayName} secondary={` ${price}`} />
                </ListItem>
              )
            })}
          </List>
        </CustomTabPanel>
      ))}
      {!store.selectedAddress && (
        <Modal
          open={true}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box sx={style}>
            <Typography sx={{ marginBottom: 2 }} id="modal-modal-title" variant="h6" component="h2">
                
            </Typography>
            <FormControl>
              <RadioGroup
                aria-labelledby="demo-radio-buttons-group-label"
                defaultValue="female"
                name="radio-buttons-group"
                value={store.selectedAddress}
                onChange={store.handleChangeAddress}
              >
                {Object.values(store.addresses).map((address) => {
                  return <FormControlLabel value={address.value} control={<Radio />} label={address.title} />
                })}
              </RadioGroup>
            </FormControl>
          </Box>
        </Modal>
      )}
      {telegram.colorScheme}
    </div>
  );
});
