import { makeAutoObservable } from "mobx";
import { ChangeEvent } from "react";
import { telegram } from "telegram";
import { AppDomain } from "app-domain";

type Address = "avtozavod" | "dimano";

export class Store {
  public source: AppDomain.View | null = null;
  public selectedAddress: Address | null = null;
  public addresses: Record<Address, { value: Address; title: string }> = {
    avtozavod: {
      value: "avtozavod",
      // title: ". ,  , .  . 3",
      title: "  ",
    },
    dimano: {
      value: "dimano",
      // title: ". ,  , .   . 335",
      title: "  ",
    },
  };

  constructor() {
    makeAutoObservable(this);
  }

  public handleChangeAddress = (event: ChangeEvent<HTMLInputElement>) => {
    this.selectedAddress = event.target.value as Address;
  };

  public init = async () => {
    console.log("start");
    const source = await fetch("/data.json")
      .then((res) => res.json())
      .catch(() => {});

    console.log(source);

    this.source = source;
    telegram?.expand?.();
  };
}

export const store = new Store();
store.init();
